var keyboard = Keyboard();
keyboard.init();
function Vector(x, y) {
    return {
        x: x,
        y: y,
    };
}
var Bullet = function (x, y) {
    var canvas = document.getElementById("canvas");
    var context = canvas.getContext("2d");
    var y = y;
    var x = x;
    var update = function () {
        if (keyboard.SPACE()) {
            y--;
        }
    };
    var draw = function () {
        // console.log('y', y)
        context.beginPath();
        context.moveTo(x, y);
        context.lineTo(x, 50);
        context.stroke();
    };
    return {
        draw: draw,
        update: update,
    };
};
var Player = function (x, y) {
    var canvas = document.getElementById("canvas");
    var context = canvas.getContext("2d");
    var defaults = {
        x: x,
        y: y,
    };
    var x = defaults.x;
    var y = defaults.y;
    var angle = 0;
    var update = function () {
        if (keyboard.LEFT()) {
            angle--;
        }
        if (keyboard.RIGHT()) {
            angle++;
        }
        if (keyboard.DOWN()) {
            y++;
        }
        if (keyboard.UP()) {
            y--;
        }
    };
    var draw = function () {
        // context.save();
        context.save();
        context.translate(x, y);
        context.rotate((angle * Math.PI) / 180);
        context.beginPath();
        context.arc(x - defaults.x, y - 20 - defaults.y, 5, 0, 2 * Math.PI);
        context.stroke();
        context.beginPath();
        context.arc(x - defaults.x, y - defaults.y, 20, 0, 2 * Math.PI);
        // context.stroke();
        // context.save();
        context.restore();
        // // context.stroke();
        // // context.beginPath();
        // // console.log(angle)
        // context.rotate(45 * (Math.PI) / 180);
        // context.translate(0, 0);
        // console.log(angle, angle * (Math.PI) / 180)
        // context.restore();
        // context.restore();
        // context.restore();
    };
    return {
        draw: draw,
        update: update,
    };
};
function GameState() {
    var canvas = document.getElementById("canvas");
    var context = canvas.getContext("2d");
    var player1;
    var init = function () {
        player1 = Player(50, 50);
    };
    var update = function () {
        player1.update();
    };
    var draw = function () {
        // context.save();
        // context.translate(150,150);
        // context.rotate(90 * (Math.PI) / 180);
        player1.draw();
        // context.restore()
    };
    init();
    return {
        draw: draw,
        update: update,
    };
}
function Keyboard() {
    var keys = {};
    var UP, LEFT, RIGHT, DOWN, SPACE;
    var init = function () {
        window.addEventListener("keydown", function (e) {
            keys[e.keyCode] = true;
        }, false);
        window.addEventListener("keyup", function (e) {
            keys[e.keyCode] = false;
        }, false);
    };
    var update = function () {
        SPACE = keys[32];
        UP = keys[38];
        LEFT = keys[37];
        RIGHT = keys[39];
        DOWN = keys[40];
    };
    return {
        update: update,
        init: init,
        UP: function () { return UP; },
        LEFT: function () { return LEFT; },
        RIGHT: function () { return RIGHT; },
        DOWN: function () { return DOWN; },
        SPACE: function () { return SPACE; },
    };
}
window.onload = function () {
    var canvas = document.getElementById("canvas");
    var context = canvas.getContext("2d");
    var game = GameState();
    (function draw() {
        window.requestAnimationFrame(draw);
        context.clearRect(0, 0, canvas.width, canvas.height);
        context.rect(0, 0, 300, 300);
        context.stroke();
        keyboard.update();
        game.update();
        game.draw();
    })();
};
